.GallerySlider {
  position: relative;
  width: 100%; }

.GallerySlider__img {
  background-color: #e7e7e7; }

.GallerySlider__img img {
  line-height: 0;
  width: 100%;
  margin: 0; }

.GallerySlider__footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 10px 0; }

.GallerySlider__navs {
  display: flex;
  align-items: center; }

.GallerySlider__expand {
  background: none;
  background-image: url("../../../assets/images/ico/expanded.png");
  background-position: center center;
  background-size: contain;
  width: 30px;
  height: 30px;
  border: none;
  -webkit-transition: all 0.4s;
  transition: all 0.4s; }

.GallerySlider__expand:hover {
  -webkit-transform: scale(1.09);
  transform: scale(1.09);
  border: none; }

.GallerySlider__arrow {
  -webkit-transition: all 0.4s;
  transition: all 0.4s; }

.GallerySlider__arrow.left {
  background: none;
  background-image: url("../../../assets/images/ico/chevron-left.png");
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  width: 30px;
  height: 30px;
  border: none; }

.GallerySlider__arrow.right {
  background: none;
  background-image: url("../../../assets/images/ico/chevron-right.png");
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  width: 30px;
  height: 30px;
  border: none; }

.GallerySlider__arrow:hover {
  -webkit-transform: scale(1.09);
  transform: scale(1.09);
  border: none; }

.GallerySlider__position {
  color: #747474;
  font-size: 14px; }

.GallerySlider__position .current {
  font-weight: 500;
  color: #9D8855;
  font-size: 20px; }
